// <script>
  //声明数据地址

  var url,diyOption,
    wt = window.top;

  //临时混用CMD模块
  idefine('iTable',function(require,exports,module){
    $('body').on('run.udmpTable', function() {
      var bt = require('udmp/lib/bs_table'),
      _show = 1;

      var defaultOptions = {
        url : url
      };

      //拷贝自定义表格属性
      var options = $.extend(true,defaultOptions,diyOption)

      //初始化表格
      bt.init($(".contentTable"), options);

      //表格加载事件
      $('.contentTable').on('post-body.bs.table', function() {
        if (_show % 2 == 1) {
          setTimeout(wt.hideLoading, 500);
          $(".contentTable tbody tr:first").is(".no-records-found") ? _show += 2 :
          _show++;
        } else {
          _show++;
        }
        //组件加载完成，进度条完成
        if(window.top.i_web_pg.getProgress()!==0){
          window.top.i_web_pg.finish();
        }
        console.info('table组件加载完成');
        $('body').resize();
      });
    })
  })

  export default{
    data(){
        return{}
    },
    props: {
      items: {
        required: true,
        default: null
      },
      url: {
        required: true,
        default: null
      },
      options: {
        default: null
      },
      id: {
        default: 'contentTable'
      }
    },
    mounted(){
      url = this.url;
      diyOption = this.options;
      seajs.use('iTable',function(){
        $('body').trigger("run.udmpTable");
      });
    },
    methods: {
      search(){
        wt.showLoading();
        $(".contentTable").bootstrapTable('removeAll').bootstrapTable('refresh')
          .hide();
        $('.page-first').trigger('click');
        $(".contentTable").show();
      }
    }
  }

// </script>



