// <script>
  //声明数据地址
  var url,diyOption,
    wt = window.top;

  //临时混用CMD模块
  idefine('iTableTree',function(require,exports,module){
    var bt = require('udmp/lib/bs_table'),
    _show = 1;

    var defaultOptions = {
      url : url,
      pagination : false,
    };

    //拷贝自定义表格属性
    var options = $.extend(true,defaultOptions,diyOption)

    //初始化表格
    bt.initTree($(".treeTable"), options,$("#parentId").val() ? $("#parentId").val() : 0);
    //bt.initTree($("#treeTable"), options);

    //表格加载事件
    $('.treeTable').on('post-body.bs.table', function() {
      if (_show % 2 == 1) {
        setTimeout(wt.hideLoading, 500);
        $(".treeTable tbody tr:first").is(".no-records-found") ? _show += 2 :
        _show++;
      } else {
        _show++;
      }
      //组件加载完成，进度条完成
      if(window.top.i_web_pg.getProgress()!==0){
        window.top.i_web_pg.finish();
        //不推荐方式，特殊处理
        setTimeout(()=>$('#i-user-org').height($('body').outerHeight(true)-100),2000);
      }
      console.info('treetable组件加载完成');
      $('body').resize();
    });

  })

  export default{
    data(){
      return{}
    },
    props: {
      items: {
        required: true,
        default: null
      },
      url: {
        required: true,
        default: null
      },
      options: {
        default: null
      },
      id: {
        default: 'treeTable'
      }
    },
    mounted(){
      url = this.url;
      diyOption = this.options;
      seajs.use('iTableTree');
    },
    methods: {
      search(){
        wt.showLoading();
        $(".treeTable").bootstrapTable('removeAll').bootstrapTable('refresh')
          .hide();
        $('.page-first').trigger('click');
        $(".treeTable").show();
      }
    }
  }

// </script>



